.header {
    background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
    padding: 20px;
    display: flex;
    align-items: center;
    color: #fff;
}
.logo {
    height: 26.22px;
    width: 30px;
    margin-right: 6px;
}
.logo--title {
    font-size: 20px;
}
.main {
    padding: 50px 20px 20px 20px;
}
.form {
    display: grid;
    grid-template: 40px 40px / 1fr 1fr;
    gap: 6px;
}
.inputs {
    border-radius: 5px;
    border: 1px solid #D5D4D8;
    font-family: 'Karla', sans-serif;
    text-indent: 5px;
}
.button {
    grid-column: 1 / -1;
    font-weight: 700;
    border-radius: 5px;
    border: none;
    font-family: 'Karla', sans-serif;
    background: linear-gradient(90.41deg, #711F8D 1.14%, #A818DA 100%);
    color: #fff;
    cursor: pointer;
}
.meme {
    position: relative;
    margin: 15px;
}
.meme-image {
    display: block;
    margin: 0 auto 50px auto;
    width: 100%;
    max-width: 500px;
    height: auto;
    border-radius: 3px;
}
.meme--text {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 2em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000,
        0 2px 0 #000,
        2px 0 0 #000,
        0 -2px 0 #000,
        -2px 0 0 #000,
        2px 2px 5px #000;
}
.bottom {
    bottom: 0;
}

.top {
    top: 0;
}
@media only screen and (max-width: 350px) {
    .form {
        display: flex;
        flex-direction: column;
    }
    .inputs {
        height: 45px;
    }
    .button {
        height: 45px;
    }
}